.searchBox {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #ccc;
    position: fixed;
    top: 0;
    left: 0%;
}

.searchBox>input[type="text"] {
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.274);
    background-color: #fff;
    border: none;
    color: rgba(0, 0, 0, 0.637);
    padding: 1vmax 2vmax;
    width: 50%;
    outline: none;
    border-radius: 0%;
    font: 300 1.1vmax cursive;
    box-sizing: border-box;
    height: 8%;
    transition: all 0.5s;
}

.searchBox>input[type="text"]:focus {
    box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.274);
}


.searchBox>input[type="submit"]{
    height: 8%;
    border-radius: 0%;
    background-color: tomato;
    border: none;
    padding: 1vmax;
    width: 10%;
    font: 300 1.1vmax "Roboto";

    cursor: pointer;
    color: #fff;
    transition: all 0.5s;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.274);



}

.searchBox>input[type="submit"]:hover{
background-color: rgba(8, 230, 8, 0.728);
}